.about {
  gap: 80px;
  padding: 80px;
  display: flex;
  align-items: center;

  &-text {
    gap: 24px;
    display: flex;
    flex-direction: column;
  }

  &-title {
    font-size: 36px;
    font-weight: 700;
    color: #333333;
  }

  &-subtitle {
    font-size: 16px;
    line-height: 25px;
    color: #4F4F4F;

    .blue {
      font-weight: 700;
      color: #457ADE;
    }
  }

  &-founders {
    gap: 24px;
    display: flex;
    align-items: center;

    &-card {
      gap: 20px;
      display: flex;
      flex-direction: column;

      img {
        border-radius: 8px;
      }
    }

    &-text {
      gap: 8px;
      display: flex;
      flex-direction: column;
    }
    
    &-name {
      font-size: 18px;
      font-weight: 700;
    }

    &-role {
      font-size: 15px;
      color: #BDBDBD;
    }
  }
}