.top {

  &-bg {
    background-color: black;
  }
  &-img {
    aspect-ratio: 16 / 9;
    background: url("../../assets/images/header-bg.png") lightgray 50% / cover no-repeat;
    opacity: 0.5;
  }
}

.header {
  padding: 40px 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &-wrapper {
    aspect-ratio: 16 / 9;
    position: absolute;
    z-index: 1;
    left: 0;
    right: 0;
    gap: 160px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &-logo {
    height: 40px;
  }

  &-links {
    gap: 64px;
    display: flex;
  }

  &-link {
    font-size: 16px;
    color: #FFFFFF;
  }
}

.text {
  gap: 24px;
  width: 50%;
  display: flex;
  flex-direction: column;
  
  &-wrapper {
    gap: 32px;
    padding: 80px;
    display: flex;
    flex-direction: column;
  }

  &-title {
    font-size: 48px;
    font-weight: 700;
    color: #FFFFFF;
    text-transform: uppercase;
  }
  
  &-subtitle {
    font-size: 18px;
    line-height: 25px;
    color: #FFFFFF;
  }
}

.button-about {
  padding: 20px 32px;
  width: fit-content;
  border-radius: 12px;
  background: linear-gradient(90deg, #457ADE 0%, #239EF8 100%);
  font-size: 16px;
  font-weight: 500;
  color: #FFFFFF;
  cursor: pointer;
}