.wish {
  &-container {
    gap: 40px;
    padding: 80px;
    display: flex;
    flex-direction: column;
  }

  &-done {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-title {
      font-size: 36px;
      font-weight: 700;
      color: #333333;
    }

    &-link {
      font-size: 16px;
      font-weight: 500;
      cursor: pointer;
      background: linear-gradient(90deg, #457ADE 0%, #239EF8 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }

  &-wrapper {
    gap: 32px;
    display: flex;
  }

  &-card {
    gap: 24px;
    width: calc(100% / 3);
    display: flex;
    flex-direction: column;

    img {
      height: fit-content;
      border-radius: 8px;
    }
  }

  &-bottom {
  }

  &-text {
    gap: 16px;
    display: flex;
    flex-direction: column;
  }

  &-watch-btn {
    padding: 8px 16px;
    width: fit-content;
    border-radius: 16px;
    background: linear-gradient(90deg, #457ADE 0%, #239EF8 100%);
  }
}